import {
    GET_BOOKS_BY_ID_REQUEST,
    GET_BOOKS_BY_ID_SUCCESS,
    GET_BOOKS_BY_ID_FAIL
} from '../constants/constants';

const getBook = (dispatch) => {
    dispatch({type: GET_BOOKS_BY_ID_REQUEST});
    fetch('https://the-one-api.dev/v2/movie', {
        headers: {
            'Authorization': 'Bearer T-U7hjPHWubzN6BWktJR'
        }
    })
        .then(res => res.json())
        .then(res => {
            if(res.error) {
                dispatch({type: GET_BOOKS_BY_ID_FAIL, payload: res.error});
            }
            dispatch({type: GET_BOOKS_BY_ID_SUCCESS, payload: res});
         })
         .catch(error => {
             dispatch({type: GET_BOOKS_BY_ID_FAIL, payload: error});
         })
         
};



export const getBookFunc = dispatch => {
    return() => getBook(dispatch);
}